import React from "react";
import Drawer from "@mui/material/Drawer";
import { useDispatch, useSelector } from "react-redux";
import { List, ListItem } from "@mui/material";
import { setDrawerLanguageOpen } from "../../redux/reducers/Main";
import rus from "../../assets/images/rus.png";
import usa from "../../assets/images/usa.png";
import { useLocation } from "react-router";
import { makeStyles } from "@mui/styles";

export default function DrawerLanguage({ t, i18n }) {
  const { isDrawerLanguageOpen } = useSelector(
    ({ MainReducer }) => MainReducer
  );

  const classes = useStyles();
  const dispatch = useDispatch();
  const location = useLocation();

  const languageMap = {
    en: { label: "ENG", dir: "ltr", active: true, img: usa },
    ru: { label: "RUS", dir: "ltr", active: false, img: rus },
  };

  return (
    <Drawer
      open={isDrawerLanguageOpen}
      onClose={() => {
        dispatch(setDrawerLanguageOpen(false));
      }}
      anchor={"bottom"}
      classes={{ paper: classes.paper }}
    >
      <List className={classes.nav}>
        {Object.keys(languageMap)?.map((item) => (
          <ListItem
            button
            key={item}
            style={{ color: "white", fontWeight: "500", paddingLeft: 24 }}
            onClick={() => {
              i18n.changeLanguage(item);
              dispatch(setDrawerLanguageOpen(false));
              document.documentElement.lang = item;
              localStorage.setItem("i18nextLng", item);
              window.location.pathname = `${item}${location.pathname}`;
            }}
          >
            <img
              width="32"
              height="32"
              src={languageMap[item].img}
              style={{
                objectFit: "contain",
                marginRight: 8,
              }}
              alt=""
            />
            {languageMap[item].label}
          </ListItem>
        ))}
      </List>
    </Drawer>
  );
}

const useStyles = makeStyles((theme) => ({
  nav: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
    backgroundColor: "#121212",
    padding: "16px 0",
  },

  paper: {
    width: "100%",
  },
}));
