import React from "react";
import { Popover, List, ListItem, Button } from "@mui/material";
import { useLocation } from "react-router";
import { makeStyles } from "@mui/styles";

const LanguageSelect = ({ i18n }) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [state, setState] = React.useState(false);

  const location = useLocation();
  const classes = useStyles();
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setState(true);
    document.body.classList.add("scrolled-body");
  };

  const handleClose = () => {
    setAnchorEl(null);
    setState(false);
    document.body.classList.remove("scrolled-body");
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const languageMap = {
    en: { label: "ENG", dir: "ltr", active: true },
    ru: { label: "RUS", dir: "ltr", active: false },
  };
  return (
    <>
      <Button
        size="small"
        sx={{}}
        className={
          state ? "languageButton languageButton_active" : "languageButton"
        }
        onClick={handleClick}
      >
        <span
          style={{
            color: "#fff",
            fontWeight: 600,
            marginLeft: 10,
            fontSize: 16,
          }}
        >
          {i18n.resolvedLanguage === "ru" ? "RUS" : "ENG"}
        </span>
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        className={classes.popover}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
      >
        <List
          style={{
            backgroundColor: "#1c1c21",
          }}
        >
          {Object.keys(languageMap)?.map((item) => (
            <ListItem
              button
              key={item}
              style={{ color: "white", fontWeight: "500" }}
              onClick={() => {
                i18n.changeLanguage(item);
                document.documentElement.lang = item;
                localStorage.setItem("i18nextLng", item);
                window.location.pathname = `${item}${location.pathname}`;
                handleClose();
              }}
            >
              {languageMap[item].label}
            </ListItem>
          ))}
        </List>
      </Popover>
    </>
  );
};

export default LanguageSelect;

const useStyles = makeStyles((theme) => ({
  popover: {
    "& .MuiPaper-root": {
      backgroundColor: "transparent!important",
    },
  },
}));
