import React from "react";
import { Route, Switch } from "react-router";
import Header from "../../shared/Header";
import Footer from "../../shared/Footer";
import LandingDrawer from "../../components/LandingDrawer";
import NotFound from "../../pages/NotFound";
import { useMediaQuery, useTheme } from "@mui/material";
const Login = React.lazy(() => import("../../pages/authentication/Login"));
const Register = React.lazy(() =>
  import("../../pages/authentication/Register")
);
const Reset = React.lazy(() => import("../../pages/authentication/Reset"));
const Activation = React.lazy(() =>
  import("../../pages/authentication/activation")
);

const AuthRoute = ({ t, i18n, mode }) => {
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(1168));
  return (
    <>
      <Header t={t} i18n={i18n} colorMode={mode} />
      <Switch>
        <Route
          exact
          path="/accounts/login"
          component={() => <Login t={t} i18n={i18n} />}
        />
        <Route
          exact
          path="/accounts/activation"
          component={() => <Activation t={t} i18n={i18n} />}
        />
        <Route
          exact
          path="/accounts/register"
          component={() => <Register t={t} />}
        />
        <Route exact path="/accounts/reset" component={() => <Reset t={t} />} />
        <Route path="*" exact={true} component={NotFound} />
      </Switch>
      <Footer t={t} />
      {tablet && <LandingDrawer t={t} i18n={i18n} />}
    </>
  );
};

export default AuthRoute;
