import React, { useEffect, useState } from "react";
import logo from "../../assets/images/header/logo.webp";
import { NavLink, useHistory, useLocation } from "react-router-dom";
import { Button, Container, useMediaQuery, IconButton } from "@mui/material";
import "./index.css";
import menu from "../../assets/images/header/menu.svg";
import { useTheme } from "@mui/material";
import LanguageSelect from "../../components/language";
import { useDispatch, useSelector } from "react-redux";
import cookie from "cookie_js";
import { setDrawerOpen, setMode } from "../../redux/reducers/Main";
import { Box } from "@mui/system";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import { makeStyles } from "@mui/styles";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";

const Header = ({ t, i18n, colorMode }) => {
  const dispatch = useDispatch();
  const location = useLocation();
  const theme = useTheme();
  const classes = useStyles();
  const tablet = useMediaQuery(theme.breakpoints.down(1168));
  const mobile = useMediaQuery(theme.breakpoints.down(576));
  const xs = useMediaQuery(theme.breakpoints.up(1450));

  // eslint-disable-next-line no-unused-vars
  const [sticky, setSticky] = useState(true);

  const token = cookie.get("token_netex");

  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );


  const { isDrawerOpen } = useSelector(({ MainReducer }) => MainReducer);

  // const makeSticky = () => {
  //   if (window.scrollY > 100) {
  //     setSticky(true);
  //   } else {
  //     setSticky(false);
  //   }
  // };

  // window.addEventListener("scroll", makeSticky);

  return (
    <header className={sticky ? "header header_sticky" : "header"}>
      <Container
        maxWidth="lg"
        style={{
          height: "100%",
          position: "relative",
          maxWidth: xs && "78.6vw",
        }}
      >
        <Box
          component="nav"
          className="nav"
          display="flex"
          justifyContent="space-between"
        >
          <Box display="flex" alignItems="center" height="100%">
            <NavLink to="/">
              <img
                src={logo}
                style={
                  mobile ? { height: 34 } : { height: 42, marginRight: 45 }
                }
                alt="logo"
              />
            </NavLink>
            {!tablet && (
              <>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/about"
                >
                  {t("About")}
                </NavLink>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/news"
                >
                  {t("News")}
                </NavLink>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/FAQ"
                >
                  FAQ
                </NavLink>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/reviews"
                >
                  {t("Reviews")}
                </NavLink>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/license"
                >
                  {t("license")}
                </NavLink>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/nft"
                >
                  {t("Netex NFT")}
                  <div
                    style={{
                      backgroundColor: "#f00",
                      objectFit: "contain",
                      position: "absolute",
                      bottom: 2,
                      right: 0,
                      padding: 4,
                      borderRadius: 4,
                      fontSize: 10,
                    }}
                  >
                    Скоро!
                  </div>
                </NavLink>
                <NavLink
                  className={classes.link}
                  activeClassName={classes.linkActive}
                  to="/main/payment-for-services/binance-pay"
                >
                  Binance Pay
                  <div
                    style={{
                      backgroundColor: "#f00",
                      objectFit: "contain",
                      position: "absolute",
                      bottom: 2,
                      right: 0,
                      padding: 4,
                      borderRadius: 4,
                      fontSize: 10,
                    }}
                  >
                    NEW!
                  </div>
                </NavLink>
              </>
            )}
          </Box>
          {!tablet && (
            <Box
              display="flex"
              alignItems="center"
              maxWidth="50%"
              overflow="hidden"
            >
              {!token && (
                <>
                  <NavLink to="/accounts/login">
                    <Button size="small" color="primary">
                      {t("LogIn")}
                    </Button>
                  </NavLink>
                  <NavLink style={{ marginLeft: 24 }} to="/accounts/register">
                    <Button variant="contained" size="small" color="primary">
                      {t("Registration")}
                    </Button>
                  </NavLink>
                </>
              )}
              <LanguageSelect t={t} i18n={i18n} classes={classes} />
              {/* <IconButton
                sx={{ ml: 1 }}
                onClick={colorMode.toggleColorMode}
                color="primary"
              >
                {theme.palette.mode === "dark" ? (
                  <Brightness7Icon />
                ) : (
                  <Brightness4Icon />
                )} 
              </IconButton> */}
              {/* <MobileApp t={t} /> */}
              {token && (
                <NavLink
                  to="/dashboard/main"
                  style={{
                    display: "flex",
                    alignItems: "center",
                  }}
                >
                  {!profileData?.data?.profile?.avatar ? (
                    <AccountCircleIcon
                      sx={{ width: "40px!important", height: "40px!important" }}
                      color="primary"
                    />
                  ) : (
                    <img
                      src={profileData?.data?.profile?.avatar}
                      style={{
                        width: 40,
                        height: 40,
                        marginLeft: 36,
                        object: "cover",
                        borderRadius: "100%",
                      }}
                      alt=""
                    />
                  )}
                </NavLink>
              )}
            </Box>
          )}

          {tablet && (
            <div
              style={{
                display: "flex",
                alignItems: "center",
              }}
            >
              {location.pathname !== "/accounts/login" && (
                <Box
                  style={{ margin: "0", zIndex: 10, display: "block" }}
                  component={NavLink}
                  onClick={() => dispatch(setDrawerOpen(false))}
                  to="/dashboard/main"
                >
                  {!profileData?.data?.profile?.avatar ? (
                    <AccountCircleIcon
                      sx={{
                        width: "50px!important",
                        mt: 1,
                        height: "50px!important",
                      }}
                      color="primary"
                    />
                  ) : (
                    <img
                      style={{
                        width: "50px",
                        height: "50px",
                        borderRadius: "50%",
                        objectFit: "cover",
                        objectPosition: "top",
                      }}
                      src={profileData?.data?.profile?.avatar}
                      alt=""
                    />
                  )}
                </Box>
              )}

              <IconButton
                onClick={() => dispatch(setDrawerOpen(!isDrawerOpen))}
                style={{ color: "#fff" }}
              >
                <img
                  src={menu}
                  style={
                    isDrawerOpen
                      ? {
                          transform: "rotate(-90deg)",
                          zIndex: 1000000,
                          transition: "all 0.2s linear",
                        }
                      : {
                          transition: "all 0.2s linear",
                        }
                  }
                  className="burger"
                  alt="burger-menu"
                />
              </IconButton>
            </div>
          )}
        </Box>
      </Container>
    </header>
  );
};

export default Header;

